ks-order-login {
    display: block;
    padding: 30px;
    background-color: #f2f2f2;
    text-align: center;
}

@media (min-width: 1200px) {
    ks-order-login {
        padding: 40px;
    }
}

ks-order-login .contents {
    max-width: 500px;
    margin: auto;
}

ks-order-login .recovery {
    display: block;
    margin-top: 10px;
}

ks-order-login .contents h1 {
    font-weight: 700;
    font-size: 28px;
}