import { Component, h, Element, Prop, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-login',
  styleUrl: 'orderLogin.css'
})
export class OrderLogin {

    @Element() root: HTMLElement;

    @Prop() api: string = "";
    @Prop() action: string = "";
    @Prop() passwordMessage: string = "";
    @Prop() passwordRecovery: string = "";

    @Prop() heading: string = "";
    @Prop() login: string = "";
    @Prop() passwordRecoveryString: string = "";
    @Prop() cartProductsMessage: string = "";

    @Prop() promptHeading: string = "";
    @Prop() promptToCart: string = "";
    @Prop() promptToConfirmation: string = "";

    render() {
        return <Host>
            <section class="contents">
                <h1>{this.heading}</h1>

                <form onSubmit={(event)=>this.Submit(event)}>
                    <slot />

                    <ks-button submit name={this.login} />
                </form>

                <a class="recovery" href={this.passwordRecovery}>
                    {this.passwordRecoveryString}
                </a>
            </section>
            <ks-order-login-prompt 
                heading={this.promptHeading}
                toCart={this.promptToCart}
                toConfirmation={this.promptToConfirmation}
            />
        </Host>;
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const email = form.querySelector("ks-input-dynamic, ks-input-text[email]") as HTMLKsInputDynamicElement;
        const pass = form.querySelector("ks-input-text[password]") as HTMLKsInputTextElement;

        await email.Validate();
        await pass.Validate();

        if (await email.IsValid() && await pass.IsValid()) {
            var headers = new Headers();
            headers.append('pragma', 'no-cache');
            headers.append('cache-control', 'no-cache');

            const formData = new FormData(form);

            const response = await fetch(this.api, {
                body: formData,
                method: 'POST',
                headers: headers,
                credentials: "same-origin"
            }).then(response => response.text());

            if(response == "false")
                pass.message = this.passwordMessage;

            else if(response == "true") {
                this.LoginPrompt(formData);
            }
        }
    }

    async LoginPrompt(data: FormData) {
        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        const formData = new FormData();
        formData.set("data", `email=${encodeURIComponent(data.get("email").toString())}&haslo=${encodeURIComponent(data.get("haslo").toString())}`)

        const response = await fetch(this.action, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        }).then(response => response.text());

        const dialog = this.root.querySelector("ks-order-login-prompt");

        if(response.search("koszyk.html") != -1) dialog.show(this.cartProductsMessage);
        else dialog.show();
    }
}